import { Box, Typography, Avatar } from '@mui/material';
import { BannerBox } from '../../CustomStyles';

const Banner = () => {
    return (
        <BannerBox>
            <Box sx={{ maxWidth: '500px' }}>
                {/* <BannerTypography>Zitky</BannerTypography>
                <BannerTypography>Svatby</BannerTypography>
                <BannerTypography>Vzpomnky</BannerTypography> */}
                <Avatar alt="LucieFojtova" src='/Profil.jpg' sx={{ width: 350, height: 350 }} />
            </Box>
            <Box sx={{ maxWidth: '700px' }}>
                <Typography variant='body1' sx={{ padding: '0px 0px 35px 0px', textAlign: 'center', maxWidth: '500px', margin: '0 auto' }}>
                    Ahoj! Jsem Lucie a ji nkolik let miluji zvuk zmkut spout. V hledku svho fotoapartu mm nejastji rodinky, miminka a svatby. 
                </Typography>
                <Typography variant="h5" sx={{ padding: '0px 15px', textAlign: 'center' }}>
                    Jedin fotografie, kter budete litovat, je ta, kterou nemte.
                </Typography>
            </Box>
        </BannerBox>
    );
};

export default Banner;