import { Box, Typography, Grid } from '@mui/material';
import MinorHeader from '../components/Header/MinorHeader';
import PriceItem from '../components/PriceList/PriceItem';
import { styleBox, PortfolioButton } from '../CustomStyles';

const svatby = {
  text: (
    <>
      <p>Ke kadmu focen pistupuji individuln.</p>
      <p>V ppad Vaeho zjmu o zasln cenov nabdky mi prosm napite zprvu spolen s termnem a mstem konn svatby.</p>
    </>
  ),
  price: "zan na 13.900 K",
};

const miminka = {
  text: (
    <>
      <p>Lifestylov nebo stylizovan focen</p>
      <p>10ks upravench fotografi v elektronick podob</p>
      <p>2-4 hodiny </p>
    </>
  ),
  price: (
    <>
      <p>Focen u vs doma: 2.900 K</p>
      <p>Atelir: 3.900 K</p>
    </>
  ),
};

const deti = {
  text: (
    <>
      <p>10 ks upravench fotografi v elektronick podob</p>
      <p>1 hodina focen </p>
    </>
  ),
  price: (
    <>
      <p>Exterir: 2.400 K</p>
      <p>Atelir: 3.400 K</p>
    </>
  ),
};

const rodiny = {
  text: (
    <>
      <p>Pry, thulky, portrty</p> 
      <p>10 ks upravench fotografi v elektronick podob</p>
      <p>1 hodina focen </p>
    </>
  ),
  price: (
    <>
      <p>Exterir: 2.400 K</p>
      <p>Atelir: 3.400 K</p>
    </>
  ),
};

const PriceList = () => {
  return(
    <>
      <Box sx={styleBox}>
        <MinorHeader minHeight="40px"/>
        <Grid container justifyContent="center" >
          <Grid item>
            <PriceItem title="svatby" text={svatby.text} price={svatby.price}/>
          </Grid>
          <Grid item>
            <PriceItem title="miminka" text={miminka.text} price={miminka.price}/>
          </Grid>
          <Grid item>
            <PriceItem title="dti" text={deti.text} price={deti.price}/>
          </Grid>
          <Grid item>
            <PriceItem title="rodinky" text={rodiny.text} price={rodiny.price}/>
          </Grid>
        </Grid>
        <Box sx={{ paddingTop: '70px' }}>
          <Typography variant="h6">Fotografie si vybrte z nhledov online galerie.</Typography>
          <br />
          <Typography variant="h6">Kad dal fotografie navc + 200k</Typography>
          <br />
          <Typography variant="h6">Cesta do 15km od Holic zdarma, pot + 9k/km</Typography>
          <br />
          <Typography variant="h6">Cenk je platn od 01.04.2023  </Typography>
        </Box>
        <PortfolioButton variant='contained' href="/kontakt">
            Napite mi
        </PortfolioButton>
      </Box>
    </>
  );
};

export default PriceList;
